import React from 'react'

const SelectFilter = ({ onPublic, filteringPublic }) => {

  return (
    <div>
      <select value={onPublic} onChange={filteringPublic}>
        <option value=''></option>
        <option value='true'></option>
        <option value='false'></option>
      </select>
    </div>
  )
}

export default SelectFilter
